import React, {useState, useEffect} from 'react'
import { Navbar, Nav, NavDropdown, Col, Container, Row} from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookSquare, faLinkedin, faGithubSquare, faInstagramSquare} from '@fortawesome/free-brands-svg-icons'

function NaviBar(){
    
    function scroll(page){
        const isSmoothScrollSupported = ((document || {}).documentElement || {}).style
        if (isSmoothScrollSupported) {
                document.querySelector(page).scrollIntoView({behavior: 'smooth',block:'start'});
        } else {
            document.querySelector(page).scrollIntoView();
        }
    }

    return(
        <Navbar style={{background:"transparent", maxWidth:"100%"}} sticky="top" className="justify-content-start" expand="lg" xs="3">
                <Col xs={6}>
                    <Nav className="justify-content-start">
                            <Nav.Item style={{display:"inline-block"}}>
                                <Nav.Link onClick={() => scroll('.about-me-page')}><span id="anctag">About Me</span></Nav.Link>
                            </Nav.Item>
                            <Nav.Item style={{display:"inline-block"}}>
                                <Nav.Link onClick={() => scroll('.portfolio-page')}><span id="anctag">Projects</span></Nav.Link>
                            </Nav.Item>
                            <Nav.Item style={{display:"inline-block"}}>
                                <Nav.Link onClick={() => scroll('.contact-page')}><span id="anctag">Contact Me!</span></Nav.Link>
                            </Nav.Item>
                    </Nav>
                </Col>
                <Col xs={6}>
                    <Nav className="justify-content-end">
                        <Nav.Item>
                            <Nav.Link target="_blank" href="https://github.com/ayushnpatel">
                                <FontAwesomeIcon icon={faGithubSquare} size="2x" className="smicons Github"></FontAwesomeIcon>
                            </Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link target="_blank" href="https://www.facebook.com/ayush.patel.927">
                                <FontAwesomeIcon icon={faFacebookSquare} size="2x" className="smicons Facebook"></FontAwesomeIcon>
                            </Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link target="_blank" href="https://www.linkedin.com/in/ayush-patel-725832197/">
                                <FontAwesomeIcon icon={faLinkedin} size="2x" className="smicons Linkedin"></FontAwesomeIcon>
                            </Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link target="_blank"  href="https://www.instagram.com/ayush.127/">
                                <FontAwesomeIcon icon={faInstagramSquare} size="2x" className="smicons Instagram"></FontAwesomeIcon>
                            </Nav.Link>
                        </Nav.Item>
                    </Nav>
                </Col>
            
        </Navbar>
    )
}

export default NaviBar;